import React from 'react';
import PropTypes from 'prop-types';
import _ from 'lodash';

// Pagination is a reusable component. Therefore we have created a list of PropTypes that must be
// passed in order for valid implementation of this component. These are the Pagination.propTypes rules

const Pagination = ({
    itemsCount,
    itemsPerPage,
    currentPage,
    onPageChange
}) => {
    // Extracting pageCount dividing itemsCount with numbers of movies we want to show per page
    const pageCount = Math.ceil(itemsCount / itemsPerPage);
    // Returning / removing pagination if there is only one page
    if (pageCount === 1) return null;
    // Creating an array with lodash from 1 to the pageCount adding one in the end
    const pages = _.range(1, pageCount + 1);

    return (
        <nav aria-label="Page navigation example">
            <ul className="pagination">
                {pages.map(pageIndex => (
                    <li
                        key={pageIndex}
                        className={
                            pageIndex === currentPage
                                ? 'page-item active'
                                : 'page-item'
                        }
                    >
                        <a
                            className="page-link"
                            onClick={() => onPageChange(pageIndex)}
                        >
                            {pageIndex}
                        </a>
                    </li>
                ))}
            </ul>
        </nav>
    );
};

Pagination.propTypes = {
    itemsCount: PropTypes.number.isRequired,
    itemsPerPage: PropTypes.number.isRequired,
    currentPage: PropTypes.number.isRequired,
    onPageChange: PropTypes.func.isRequired
};

export default Pagination;



