import axios from 'axios';
import logger from './logService';
import { toast } from 'react-toastify';

// Setting default base url from .env files
axios.defaults.baseURL = process.env.REACT_APP_API_URL;

// Handling http errors globally.
axios.interceptors.response.use(null, error => {
  // Check if we are dealing with an expected error (Errors in the 400 range, status: 400 - 500)
  // Unexpexted (network down, server down, database down, bug)
  // - Log them
  // - Display a generic and friendly error message
  const expectedError =
    error.response &&
    error.response.status >= 400 &&
    error.response.status < 500;

  // If we are dealing with an UNEXEPECTED ERROR
  if (!expectedError) {
    logger.log(error);
    toast.error('Unexpected error occured');
  }

  // If we are dealing with an expected error, this is handle locally in our fetch function
  // Expected (404: not found, 400: bad request) - CLIENT ERRORS
  // - Display specific error message to user

  // We end this interceptors function by passing the rejected promise
  // to our catch block in the local fetch function
  return Promise.reject(error);
});

function setJwt(jwt) {
  // Setting common header for http request
  axios.defaults.headers.common['x-auth-token'] = jwt;
}

export default {
  get: axios.get,
  post: axios.post,
  put: axios.put,
  patch: axios.patch,
  delete: axios.delete,
  setJwt
};



