import React, { Component } from 'react';

import Joi from 'joi-browser';
import Input from './input';
import Select from './select';

class Form extends Component {
    state = {
        data: {},
        errors: {}
    };

    validate = () => {
        const options = {
            abortEarly: false
        };
        const { error } = Joi.validate(this.state.data, this.schema, options);
        if (!error) return null;

        const errors = {};
        // Getting error messages from Joi and mapping them to our errors obj
        for (let item of error.details) errors[item.path[0]] = item.message;
        return errors;
    };

    validateProperty = ({ name, value }) => {
        const obj = { [name]: value };
        const schema = { [name]: this.schema[name] };
        const { error } = Joi.validate(obj, schema);
        return error ? error.details[0].message : null;
    };

    handleSubmit = e => {
        e.preventDefault();

        const errors = this.validate();
        this.setState({ errors: errors || {} });

        // If errors return immediately before contacting server
        if (errors) return;
        this.doSubmit();
    };

    handleChange = ({ currentTarget: input }) => {
        // Cloning state.errors to new const
        const errors = { ...this.state.errors };
        // Generating error message from validateProperty method
        const errorMessage = this.validateProperty(input);

        // If there is an error message set errors[input.name] to the errorMessage
        if (errorMessage) errors[input.name] = errorMessage;
        // else just delete if from the errors object
        else delete errors[input.name];

        const data = { ...this.state.data };
        data[input.name] = input.value;

        // Set state again with the new data and errors object
        this.setState({ data, errors });
    };

    renderButton(label) {
        return (
            <button disabled={this.validate()} className="btn btn-primary">
                {label}
            </button>
        );
    }

    renderInput(name, label, type = 'text') {
        const { data, errors } = this.state;
        return (
            <Input
                type={type}
                name={name}
                label={label}
                value={data[name]}
                onChange={this.handleChange}
                error={errors[name]}
            />
        );
    }

    renderSelect(name, label, options) {
        const { data, errors } = this.state;

        return (
            <Select
                name={name}
                value={data[name]}
                label={label}
                options={options}
                onChange={this.handleChange}
                error={errors[name]}
            />
        );
    }
}

export default Form;



