import Raven from 'raven-js';

function init() {
  // Raven error tracking with sentry.io
  Raven.config('https://5f51a9f41a174365b18445c676177114@sentry.io/1268336', {
    release: '1-0-0',
    environment: 'development-test'
  }).install();
}

function log(error) {
  Raven.captureException(error);
}

export default {
  init,
  log
};



