import http from './httpService';

const apiEndPoint = '/movies';

function movieUrl(id) {
  return `${apiEndPoint}/${id}`;
}

export function getMovies() {
  return http.get(apiEndPoint);
}

export function getMovie(movieId) {
  return http.get(movieUrl(movieId));
}

export function deleteMovie(movieId) {
  return http.delete(movieUrl(movieId));
}

export function saveMovie(movie) {
  // NOTE: When saving the movie we pass the movie ID in the url
  // but we also pass the full movie object as the second parameter
  // This can confuse the backend about which ID value it should use for deleting
  // since there is two. Therefore we remove the id from the body object before sending it
  if (movie._id) {
    // Clone movie
    const body = { ...movie };
    // Deleting id from body clone
    delete body._id;
    // Sending data to be updated
    return http.put(movieUrl(movie._id), body);
  }

  // If the movie has not got a movie id it does not exists. This means we are creating a new movie
  // Sending the movie body object with post to our endpoint
  return http.post(apiEndPoint, movie);
}



