import React from 'react';

const Filter = ({
    items,
    textProperty,
    valueProperty,
    onItemSelect,
    selectedItem
}) => {
    return (
        <ul className="list-group">
            {items.map(item => (
                // Note that we are using dynamic properties and therefore we use
                // the [propertyName] instead of the "item.propertyName" dot notation
                <li
                    onClick={() => onItemSelect(item)}
                    key={item[valueProperty]}
                    className={
                        item === selectedItem
                            ? 'list-group-item active'
                            : 'list-group-item'
                    }
                >
                    {item[textProperty]}
                </li>
            ))}
        </ul>
    );
};

Filter.defaultProps = {
    // Can be overridden if setting the props where we implement the component
    textProperty: 'name',
    valueProperty: '_id'
};

export default Filter;



